*{
  margin: 0;
  padding: 0;
}
body{background-color: #0e192c;}
.App-input{
  text-align: center;
  color: rgb(187, 170, 170);
  font-size: 18px;
  margin-top: 25px;
}
input{
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  border-radius: 5px;
  border: 1px solid grey;
  margin-top: 25px;
  outline: none;
  padding: 10px 5px;
  width: 200px;
}
.App-input button{
  background-color: #1F6E8C;
  color: rgb(215, 211, 211);
  font-weight: 600;
  font-size: 15px;
  padding: 10px 20px;
  border-radius: 30px;
  margin-top: 20px;
  border: 0;
  margin-bottom: 25px;
}
.navActive{
  color: #84A7A1;
}
nav{
  padding-top: 30px;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 5px;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 60%;
  color: #1F6E8C;
  font-weight: bold;
}
nav ul li:hover{
  transform: scale(1.2)
}
nav ul li{
  cursor: pointer;
  font-size: 20px;
  transition: all 0.3s ease-in-out;
}
.App-container{
  padding-top: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color: #84A7A1; 
}